// Memoized Resource Card Component
const ResourceCard = React.memo(({ resource, isDarkMode, index, isInView }) => {
  const category = categories.find(c => c.id === resource.type);
  const animationProps = useMemo(() => ({
    initial: { opacity: 0, y: 20 },
    animate: isInView ? {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        delay: index * 0.2,
        ease: [0.215, 0.610, 0.355, 1.000]
      }
    } : { opacity: 0, y: 20 }
  }), [isInView, index]);

  // Subtle pastel gradients for each category
  const gradients = {
    ebooks: isDarkMode 
      ? 'bg-gradient-to-br from-purple-900/10 via-purple-800/5 to-transparent'
      : 'bg-gradient-to-br from-purple-50 via-purple-50/50 to-white',
    videos: isDarkMode
      ? 'bg-gradient-to-br from-emerald-900/10 via-emerald-800/5 to-transparent'
      : 'bg-gradient-to-br from-emerald-50 via-emerald-50/50 to-white',
    papers: isDarkMode
      ? 'bg-gradient-to-br from-amber-900/10 via-amber-800/5 to-transparent'
      : 'bg-gradient-to-br from-amber-50 via-amber-50/50 to-white',
    competitive: isDarkMode
      ? 'bg-gradient-to-br from-sky-900/10 via-sky-800/5 to-transparent'
      : 'bg-gradient-to-br from-sky-50 via-sky-50/50 to-white',
    all: isDarkMode
      ? 'bg-gradient-to-br from-gray-900/10 via-gray-800/5 to-transparent'
      : 'bg-gradient-to-br from-gray-50 via-gray-50/50 to-white'
  };

  return (
    <motion.div
      {...animationProps}
      className="group relative"
    >
      <div 
        className={`overflow-hidden rounded-2xl transition-all duration-300 ${
          isDarkMode 
            ? 'bg-white/5 hover:bg-white/10' 
            : 'bg-white hover:bg-white'
        } ${gradients[resource.type]}`}
      >
        {/* Animated Circle Decoration */}
        <div className="relative p-6 z-10 h-full flex flex-col justify-between backdrop-blur-[2px]">
          <div className={`absolute w-28 h-28 bg-transparent border-4 rounded-full -left-10 -top-10 transition-all duration-700 group-hover:scale-150 group-hover:translate-x-32 group-hover:translate-y-32 ${
            isDarkMode 
              ? 'border-white/10' 
              : 'border-primary/5'
          }`} />
          
          {/* Content */}
          <div className="flex flex-col gap-4">
            <div className="flex items-start justify-between">
              <h3 className={`text-lg font-semibold ${
                isDarkMode ? 'text-white' : 'text-gray-900'
              }`}>
                {resource.name}
              </h3>
              <span className={`p-2 rounded-xl backdrop-blur-sm ${
                isDarkMode 
                  ? 'bg-white/5' 
                  : 'bg-primary/5'
              }`}>
                <category.icon className={`w-5 h-5 ${
                  isDarkMode ? 'text-white/80' : 'text-primary/80'
                }`} />
              </span>
            </div>
            
            <p className={`text-sm leading-relaxed ${
              isDarkMode ? 'text-gray-400' : 'text-gray-600'
            }`}>
              {resource.description}
            </p>
          </div>

          {/* Footer */}
          <div className="mt-6">
            <div className={`flex items-center gap-4 text-sm mb-4 ${
              isDarkMode ? 'text-gray-400' : 'text-gray-500'
            }`}>
              <span className="flex items-center gap-1.5">
                <FiDownload className="w-4 h-4" />
                {resource.downloads} downloads
              </span>
              <span className="w-1 h-1 rounded-full bg-current opacity-50" />
              <span className="flex items-center gap-1.5">
                <FiStar className={`w-4 h-4 ${
                  isDarkMode ? 'text-amber-400' : 'text-amber-500'
                }`} />
                {resource.rating} rating
              </span>
            </div>

            {/* Action Buttons */}
            <div className="flex gap-2">
              <button 
                className={`flex-1 py-2.5 rounded-xl text-sm font-medium transition-colors flex items-center justify-center gap-2 backdrop-blur-sm ${
                  isDarkMode 
                    ? 'bg-white/5 hover:bg-white/10 text-white' 
                    : 'bg-primary/5 hover:bg-primary/10 text-primary'
                }`}
              >
                <svg className="w-4 h-4" viewBox="0 0 24 24" fill="none">
                  <path 
                    d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" 
                    stroke="currentColor" 
                    strokeWidth="2" 
                    strokeLinecap="round" 
                    strokeLinejoin="round"
                  />
                </svg>
                Access
              </button>
              <button 
                className={`p-2.5 rounded-xl transition-colors backdrop-blur-sm ${
                  isDarkMode 
                    ? 'bg-white/5 hover:bg-white/10 text-white' 
                    : 'bg-primary/5 hover:bg-primary/10 text-primary'
                }`}
              >
                <svg className="w-4 h-4" viewBox="0 0 24 24" fill="none">
                  <path 
                    d="M5 12h.01M12 12h.01M19 12h.01M6 12a1 1 0 11-2 0 1 1 0 012 0zm7 0a1 1 0 11-2 0 1 1 0 012 0zm7 0a1 1 0 11-2 0 1 1 0 012 0z" 
                    stroke="currentColor" 
                    strokeWidth="2" 
                    strokeLinecap="round" 
                    strokeLinejoin="round"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Subtle hover effect */}
      <div className={`absolute inset-0 rounded-2xl transition-opacity duration-300 opacity-0 group-hover:opacity-100 pointer-events-none ${
        isDarkMode
          ? 'bg-gradient-to-t from-white/5 via-transparent to-transparent'
          : 'bg-gradient-to-t from-primary/5 via-transparent to-transparent'
      }`} />
    </motion.div>
  );
});

const DigitalLibrary = ({ onLoad }) => {
  const [activeCategory, setActiveCategory] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [isJustFocused, setIsJustFocused] = useState(false);
  const { isDarkMode } = useTheme();
  const containerRef = React.useRef(null);
  const [isInView, setIsInView] = useState(false);

  // Scroll animations
  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start end", "end start"]
  });

  const headerY = useTransform(scrollYProgress, [0, 0.2], [50, 0]);
  const headerOpacity = useTransform(scrollYProgress, [0, 0.2], [0, 1]);

  // Intersection Observer for animations
  React.useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        setIsInView(entry.isIntersecting);
      },
      {
        threshold: 0.1
      }
    );

    if (containerRef.current) {
      observer.observe(containerRef.current);
    }

    return () => {
      if (containerRef.current) {
        observer.unobserve(containerRef.current);
      }
    };
  }, []);

  // Handle focus animation
  const handleFocus = () => {
    setIsJustFocused(true);
    // Remove the class after animation completes
    setTimeout(() => setIsJustFocused(false), 500);
  };

  // Memoized filtered resources
  const filteredResources = useMemo(() => {
    return resources.filter(resource => {
      const matchesCategory = activeCategory === 'all' || resource.type === activeCategory;
      const matchesSearch = searchQuery === '' || 
        resource.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        resource.description.toLowerCase().includes(searchQuery.toLowerCase());
      return matchesCategory && matchesSearch;
    });
  }, [activeCategory, searchQuery]);

  // Call onLoad when component mounts
  React.useEffect(() => {
    onLoad?.();
  }, [onLoad]);

  return (
    <section 
      id="digital-library-section"
      ref={containerRef}
      className={`relative min-h-screen flex items-center py-36 md:py-48 ${
        isDarkMode ? 'bg-gray-900' : 'bg-gradient-to-b from-white to-gray-50'
      }`}
    >
      {/* Background */}
      <div className="absolute inset-0">
        <img 
          src={digitalLibraryBackground} 
          alt="background" 
          className={`w-full h-full object-cover object-center ${
            isDarkMode ? 'opacity-30' : 'opacity-50'
          }`}
        />
        <div className={`absolute inset-0 ${
          isDarkMode 
            ? 'bg-gradient-to-br from-gray-900/90 via-gray-900/80 to-gray-900/90' 
            : 'bg-gradient-to-b from-white/60 via-white/70 to-white/60'
        }`} />
      </div>

      <div className="container relative mx-auto px-4">
        {/* Header */}
        <motion.div 
          style={{ y: headerY, opacity: headerOpacity }}
          className="text-center mb-20"
        >
          <motion.span
            className={`inline-block px-4 py-1.5 mb-6 text-sm font-medium rounded-full ${
              isDarkMode 
                ? 'text-white/90 bg-white/10' 
                : 'text-primary/90 bg-primary/10'
            }`}
          >
            Digital Library
          </motion.span>

          <h2 className={`text-4xl lg:text-5xl font-bold mb-8`}>
            <span className={isDarkMode ? 'text-white' : 'text-gray-900'}>Access </span>
            <span className={isDarkMode ? 'text-purple-400' : 'text-primary'}>Digital</span>
            <span className={isDarkMode ? 'text-white' : 'text-gray-900'}> Resources</span>
          </h2>

          <p className={`text-lg max-w-2xl mx-auto ${
            isDarkMode ? 'text-gray-300/90' : 'text-gray-600/90'
          }`}>
            Explore our curated collection of educational materials
          </p>
        </motion.div>

        {/* Search Input */}
        <div className="mb-16">
          <StyledSearch isDarkMode={isDarkMode}>
            <div className="input-container">
              <input
                className="input"
                type="text"
                placeholder="Search resources..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
              <FiSearch className="search-icon" size={18} />
            </div>
          </StyledSearch>
        </div>

        {/* Categories */}
        <div className="max-w-5xl mx-auto mb-20">
          <div className="flex flex-wrap items-center justify-center gap-3">
            {categories.map((category) => (
              <button
                key={category.id}
                onClick={() => setActiveCategory(category.id)}
                className={`relative inline-flex items-center justify-center gap-2.5 text-[15px] font-medium transition-all rounded-xl px-5 py-2.5 group
                  ${activeCategory === category.id
                    ? isDarkMode
                      ? `bg-white/15 text-white/90 ring-2 ring-${
                          category.id === 'all' ? 'gray' : 
                          category.id === 'ebooks' ? 'purple' :
                          category.id === 'videos' ? 'emerald' :
                          category.id === 'papers' ? 'amber' : 'sky'
                        }-400/40 ring-offset-2 ring-offset-gray-900`
                      : `bg-white text-gray-900 shadow-md ring-2 ring-${
                          category.id === 'all' ? 'gray' : 
                          category.id === 'ebooks' ? 'purple' :
                          category.id === 'videos' ? 'emerald' :
                          category.id === 'papers' ? 'amber' : 'sky'
                        }-500/20`
                    : isDarkMode
                      ? 'bg-white/5 hover:bg-white/10 text-gray-300/90 hover:text-white/90'
                      : 'bg-white hover:bg-gray-50 text-gray-600 shadow-sm hover:text-gray-900'
                  }`}
              >
                {/* Icon with category-specific colors */}
                <span className={`transition-colors ${
                  activeCategory === category.id
                    ? isDarkMode
                      ? category.id === 'all'
                        ? 'text-gray-200'
                        : category.id === 'ebooks'
                        ? 'text-purple-300'
                        : category.id === 'videos'
                        ? 'text-emerald-300'
                        : category.id === 'papers'
                        ? 'text-amber-300'
                        : 'text-sky-300'
                      : category.id === 'all'
                        ? 'text-gray-700'
                        : category.id === 'ebooks'
                        ? 'text-purple-600'
                        : category.id === 'videos'
                        ? 'text-emerald-600'
                        : category.id === 'papers'
                        ? 'text-amber-600'
                        : 'text-sky-600'
                    : isDarkMode
                      ? category.id === 'all'
                        ? 'text-gray-400 group-hover:text-gray-300'
                        : category.id === 'ebooks'
                        ? 'text-purple-400 group-hover:text-purple-300'
                        : category.id === 'videos'
                        ? 'text-emerald-400 group-hover:text-emerald-300'
                        : category.id === 'papers'
                        ? 'text-amber-400 group-hover:text-amber-300'
                        : 'text-sky-400 group-hover:text-sky-300'
                      : category.id === 'all'
                        ? 'text-gray-400 group-hover:text-gray-600'
                        : category.id === 'ebooks'
                        ? 'text-purple-400 group-hover:text-purple-600'
                        : category.id === 'videos'
                        ? 'text-emerald-400 group-hover:text-emerald-600'
                        : category.id === 'papers'
                        ? 'text-amber-400 group-hover:text-amber-600'
                        : 'text-sky-400 group-hover:text-sky-600'
                }`}>
                  <category.icon className="w-[18px] h-[18px]" />
                </span>

                {/* Label with slide animation */}
                <span className={`origin-left transition-all duration-200 tracking-wide ${
                  activeCategory === category.id
                    ? 'scale-100 font-semibold'
                    : 'scale-95 group-hover:scale-100'
                }`}>
                  {category.label}
                </span>
              </button>
            ))}
          </div>

          {/* Category Description */}
          <AnimatePresence mode="wait">
            <motion.p
              key={activeCategory}
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: 10 }}
              className={`text-center mt-6 text-[15px] max-w-2xl mx-auto ${
                isDarkMode ? 'text-gray-400/90' : 'text-gray-500/90'
              }`}
            >
              {categories.find(c => c.id === activeCategory)?.description}
            </motion.p>
          </AnimatePresence>
        </div>

        {/* Resources Grid */}
        <div className="grid md:grid-cols-2 gap-8 max-w-5xl mx-auto">
          <AnimatePresence mode="wait">
            {filteredResources.map((resource, index) => (
              <ResourceCard
                key={resource.name}
                resource={resource}
                isDarkMode={isDarkMode}
                index={index}
                isInView={isInView}
              />
            ))}
          </AnimatePresence>
        </div>
      </div>
    </section>
  );
};

export default React.memo(DigitalLibrary); 